import styled from 'styled-components';

const StyledButton = styled.button`
  padding: ${props => props.theme.gutter};
`;

const Section = props => {
  return (
    <StyledSection>
      {props.withContainer ? (
        <StyledContainer>
          {props.children}
        </StyledContainer>
      ) : (
        <div>
          {props.children}
        </div>
      )}
    </StyledSection>
  );
}

export default Section;
